.container.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
    font-size: larger;
    margin-bottom: 7rem;
}

.contact_options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;


}
.contact_option a{
    color: aliceblue;
    font-weight: 700;
}
.title{
    text-align: center;
    margin-top: 7%;
    margin-bottom: 4%;
}
.title h2{
    color: var(--color-primary);
    font-size: 2rem;
}

.contact_option{
    background:#3d3795;
    color: antiquewhite;
    padding: 1.2rem;
    text-align: center;
    
    border-radius: 2rem;
    transition: var(--transition);
}

.contact_option:hover{
    background-color: transparent;
    border: 1px solid antiquewhite;
    padding: 3rem;
    font-size: 1.3rem;
    
}

form{ 
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    

}

input , textarea{
    font-size: large;
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background-color: transparent;
    border: 2px solid var(--color-primary);
    resize: none;
    color: antiquewhite;
}


.sub{
    width: 100%;
    height: 60%;
    background: var(--color-primary);
    color: antiquewhite;
    padding: 1.5rem;
    border-radius: 0.4rem;
    cursor: pointer; 
    border: 1px solid var(--color-primary); 
    transition: var(--transition); 

}


.sub:hover{ 
    background: transparent; 
    font-size: larger;
    font-weight: 700;
    padding: 2rem;
    color: antiquewhite;

}


@media screen and (max-width: 1024px)
{
   .container.contact_container{
    grid-template-columns: 1fr;
    gap: 2rem;
   }
}
@media screen and (max-width: 600px)
{   
    .contact_options{
            width: 100%;
    }
    .container.contact_container{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        
       }
  
}