

.container_services{ 
    padding-top: 7rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:2rem;
    margin-left: 10%;
    width: 80%;
    color: #3d3795 ;
   
}

.service{ 
    background-color: antiquewhite;
    border-radius: 2rem; 
    border: solid 1px antiquewhite;
    width: 90%;
    height: fit-content;
    transition: var(--transition);


}
.service:hover{
    color: antiquewhite;
    background: linear-gradient(#5b42c8,#3685c2);
    border-color: #2b229e;
    backdrop-filter: blur(40px);
    cursor: defualt;
    padding: 4%;
    transition: var(--transition);
}
.service_head{ 
    background: linear-gradient(rgba(255, 255, 255, 0.3), transparent);
    padding: 2rem;
    border-radius: 2rem;
    box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);

}
.service_head h3{
    color: #2b229e ;
    font-size: 2rem;
    text-align: center;

}

.service_list{
    padding: 2rem;
    font-size: 1.5rem;
}
.service_list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}
.service_icon{
    color: #2b229e ;
}


@media screen and (max-width: 1024px)
{
    .container_services{ 
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    section{ 
        margin-top: 6rem;
    }
}
@media screen and (max-width: 600px)
{
    .container_services{ 
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-left: 0.5rem;
    }
    section{ 
        margin-top: 2rem;
    }
}