.about{
    height: 100vh;
    width: 100hv;
    padding-top: 7rem;
    overflow: hidden;
}

.about__container{
    margin-top: 5rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 3rem;

    }
.about__image{
    margin-left: 10rem; 
    width: 30%;
    height:30%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient( 45deg , transparent , var(--color-primary) , transparent);
}
.image{
    
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition); 
    
}

.first:hover{
    color: var(--color);
}
.last:hover
{
    color: var(--color-primary);
}

.image:hover{
    transform: rotate(0);
    
}
.cards{
    display: flex;
    width: 100%;
    margin-left: 3rem;
    

}
.about__cards{
    width: 30%;
    background: #5736c2;
    backdrop-filter: blur(30px);
    color: antiquewhite;
    margin: 0.7rem;
    padding : 3rem; 
    border-radius: 1.5rem;
    text-align: center;
    transition: var(--transition);

}

.about__cards:hover{

   background: antiquewhite;
   color: #433d95; 
    cursor: default; 
   /* border:4px solid  #433d95;*/
    
}

.Welcome{
    
    margin-top: 3rem;
    text-size-adjust: 200px;
    margin-bottom: 3rem;
    margin-left: 3rem;
}



.bott{
    
    display: flex;
    gap: 2%;
    margin-top: 2%;
    margin-left: 3rem;
    margin-bottom: 3rem;
}



.CVdown{
    background-color: var(--color-primary);
    color: antiquewhite;
    width: 20%;
    padding: 13px;
    padding-left: 2%;
    padding-right: 2%;
    text-align: center;
    transition: var(--transition);
}
.Cont{
    color: antiquewhite; 
    border-color:antiquewhite;
    transition: var(--transition);
    text-align: center;
    padding: 1rem;
    width: 20%;
}

.CVdown:hover{
    width: 80%;
    color: var(--color);
    background-color: transparent;
    border-color: var(--color);
}
.Cont:hover{
    
   width: 80%;
   text-align: center;
    color:antiquewhite;
    background-color: var(--color-primary);
    border:none;
    transition: var(--transition);
}


@media screen and (max-width: 1024px)
{
   .about__container{
    display: flex;
    flex-direction: column;
    grid-template-columns:  1fr;
    gap: 0; 
   
   }
   .CVdown, .Cont{
    width: 100%;
}  
.CVdown:hover, .Cont:hover{
    width: 100%;
}    
.bott{
    margin: 5%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-align: center;
    
}

   .about__image{
    width: 70%;
    margin: 2rem auto 4rem;
   }
   
   .cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
   }
   .about__cards{ 
    margin-right:2rem;
    width: 95% !important;
   }
}
@media screen and (max-width: 600px)
{
    header{
        height: 100hv; 
        
       }
    .cards{
        display: flex;
        flex-direction: column;
        margin-left: 0.5rem;
    }

    .CVdown, .Cont{
        width: 100%;
    }  
    .CVdown:hover, .Cont:hover{
        width: 100%;
    }    
    .bott{
        margin: 5%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        text-align: center;
        
    }

    .socials ,
    .scroll__down{
        display: none;
    }
    
    
}