footer{
    background: linear-gradient(#3d3795 , var(--color-primary));
    padding: 3rem 0rem;
    text-align: center  ;
    font: 0.9rem;
    margin-top: 7rem;
    padding-bottom: 10rem;

}

footer a { 
    color: antiquewhite;
    cursor: pointer;
}

.outline{ 
    display: flex;
    flex-direction: column;
    flex-wrap: warp;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    
}
.indcies{
    display: flex;
    flex-direction: row;
    flex-wrap: warp;
    gap :4rem;
    justify-content: center;
    font-size: 1.5rem;

}
.socials{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}
.socials a{ 
    color: antiquewhite;
    border-radius: 1rem;
    
}