section{
   
    height: 100hv;
    margin-top: 5rem;
}


.projects{
    padding-top: 3rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2rem;
    justify-items: center;
    text-align: center;
}
.project_item{
      background: linear-gradient(#3d37959d, transparent);
    padding: 1.3rem;
    padding-left: 4rem;
    padding-right: 4rem;
    border: 2px solid var(--color-variant);
    border-radius: 2rem;
    transition:  all 700ms ease;
    
    backdrop-filter: blur(30px);
}
.project_item:hover{
    padding: 3.4rem;
    background: linear-gradient(transparent, #3d37959d);
}
.project_item a{ 
    padding: 0.5rem;
    gap:1rem;
}
.images{

    aspect-ratio: 1/1;
    width: 350px;
    height: 342px;
    border-radius: 1.5rem;
    overflow: hidden;
}

.project_item h3{ 
    margin: 1.2rem 0 2rem;

}
.boten{
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    justify-content: center;
}

.B{
 color: antiquewhite;
 font-size: larger;
 border-radius: 2rem;
 border: 2px solid #5b42c8;
 background: #5b42c8;
 padding: 5rem;
}
.B:hover{
    background: transparent;
    color: antiquewhite;
    border-color: antiquewhite;
    cursor: pointer;
    padding-left: 15%;
}
.P{

color: antiquewhite;
 font-size: larger;
 border-radius: 2rem;
 border: 2px solid #5b42c8;
 background:#5b42c8;
 padding: 5rem;
}

.P:hover{
    width: fit-content;
    background: transparent;
    color: antiquewhite;
    border-color: antiquewhite;
    cursor: pointer;
    padding-right: 15%;
}


@media screen and (max-width: 1233px)
{
  .projects{
    grid-template-columns: 1fr 1fr;
    gap:1.2rem;
    margin: 3%;
}
}
@media screen and (max-width: 600px)
{
   .projects{
    display: flex;
    flex-direction: column;
    margin-left: 0.5rem;
    
   }
   .images{
    width: 100%;
   }
}