@import url('https://fonts.googleapis.com/css2?family=Sofia+Sans:wght@300;400;500;600&display=swap');
*{
   font-family: 'Times New Roman', Times, serif;

   margin: 0; 
   padding: 0;
   border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none ;
    font-weight: 700;

}
:root{
    --color-bg: #1f1f38;
    --color: antiquewhite /*#4d66f1 /*#684cd8*/;
    --color-variant: #3d3795;
    --color-bg-variant: #d1a350 ; 
    --color-primary: /*#f14d4d*/ #3d3795; 
    --color-primary-variant: #206494; 
    --color-white: #fff; 
    --color-light: rgba(255,255,255,0.6); 
    --gradient: linear-gradient(var(--color-primary) , #3d3795); 
    
    --container-width-lg: 75% ; 
    --container-width-md: 85%; 
    --container-width-ms: 90%; 

    --transition : all 500ms ease;

}


html { 
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none; 
}

body{ 
    color: white;
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-bg);
    line-height: 1.5;
    
    background-image: url("./assests/pattern.png"); 

}

.container{
    width: var(--container-width-lg);
    margin: 0 auto; 
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    height: 100hv;
}

section > h2, 
section >h5{ 
    text-align: center;
    color: var(--color-light);
}


section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light); 

}

a{ 
    color: var(--color-primary); 
    transition: var(--transition);
}

a:hover{
    color: var(--color-primary);

}

.btn{
    width: max-content;
    display: inline-block; 
    color: var(--color-primary); 
    border-radius: 0.4rem;
    cursor: pointer; 
    border: 1px solid var(--color-primary); 
    transition: var(--transition); 

}


.btn:hover{ 
    background: var(--color-white); 
    color : car(--color-bg); 
    /*border-color: transparent; */

}

.btn-primary { 
    background: var(--color-primary);
    color: var(--color-bg); 

}

img{

    display: block;
    width: 100%; 
    object-fit: cover; 
}


/* ==================================================*/ 

@media screen and (max-width: 1024px)
{
    .container{ 
        width: var(max-width-md);

    }
    section{ 
        margin-top: 6rem;
    }
}
@media screen and (max-width: 600px)
{
    .container{ 
        width: var(max-width-sm);

    }
    section{ 
        margin-top: 2rem;
    }
}